import React, { Fragment } from "react";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, Feature, RateLimit } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";

export default function RateLimitModal() {
  const { licensed } = useAppStore();
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/ratelimit", "create", {} as RateLimit);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Rate Limit" />
      </ModalOpenButton>
      <ModalContent title="Create new rate limit" onClickOk={handleOk} formName="create_ratelimit_form">
        <Form schemaName="ratelimit">
          <FormContentBase
            name="create_ratelimit_form"
            form={form}
            preserve={false}
            defaultValues={{
              endpoint: "*",
              limit: 100,
              period: "600",
            }}
          />
          {!licensed(Feature.Api) && <BuyLicenseAlertBox />}
        </Form>
      </ModalContent>
    </Fragment>
  );
}
